.header {
  margin: 0 auto 28px;
  padding-top: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1360px;
  height: 40px;
}

.header__container {
  margin: 0;
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 24px;
  width: 89px;
  height: 40px;
}

.header__date {
  margin: 0;
  display: flex;
  font-family: "Cabinet Grotesk", "Arial", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
}

.header__nav-container {
  display: flex;
  align-items: center;
}

.header__nav-button {
  display: flex;
  height: 24px;
  font-family: "Cabinet Grotesk";
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  border: none;
  background-color: #fff;
  margin-right: 20px;
  padding: 0;
}

.header__nav-button:hover {
  cursor: pointer;
  opacity: 0.6;
}

.header__nav-user {
  height: 24px;
  font-family: "Cabinet Grotesk";
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
}

.header__nav-avatar {
  margin-left: 12px;
  height: 40px;
  width: 40px;
  box-sizing: border-box;
}
